
@import '../static/style/style-font.css';
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
dl,
dd,
span,
i,
div {
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: 100%;
}
* {
  box-sizing: border-box;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
a,
input,
button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-appearance: none;
  outline: none;
}
*:not(input,textarea) {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
/*去掉textarea ios下阴影*/
textarea {
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
  -webkit-appearance: none;
  border-radius: 0;
  outline: none;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
input::-webkit-input-placeholder {
  color: #ddd;
}
input::placeholder {
  color: #ddd;
}
textarea,
textarea::-webkit-input-placeholder {
  color: #ddd;
  font-family: '微软雅黑', 'microsoft yahei', Arial, sans-serif;
}
textarea,
textarea::placeholder {
  color: #ddd;
  font-family: '微软雅黑', 'microsoft yahei', Arial, sans-serif;
}
html {
  position: relative;
}
html,
body {
  height: 100%;
  width: 100%;
  color: #999;
}
body {
  font-family: '微软雅黑', 'microsoft yahei', Arial, sans-serif;
  background-color: #f0f0f0;
  font-size: 0.68230277rem;
}
a {
  display: block;
  color: #999;
}
#app {
  width: 100%;
  height: 100%;
}
.left {
  float: left;
}
.right {
  float: right;
}
h5 {
  font-size: 0.59701493rem;
  color: #545352;
}
h4 {
  font-size: 0.68230277rem;
  color: #545352;
}
img[lazy=loading] {
  background-color: #eee;
}
.price {
  color: red;
  font-weight: bold;
}
/*-------------------publick-------------------------*/
.pu_header {
  height: 2.13219616rem;
  line-height: 2.21748401rem;
  position: fixed;
  left: 0 ;
  right: 0;
  z-index: 500;
  background-color: #fff;
  border-bottom: 1px solid #eee;
  text-align: center;
  color: #999;
}
.pu_header span.title {
  color: #04BE02;
}
.pu_header div.bakcSpan {
  position: absolute;
  height: 100%;
  width: 1.70575693rem;
  left: 0;
  font-size: 0.85287846rem;
  text-align: center;
  line-height: 2.21748401rem;
}
.pu_header .right {
  padding-left: 0.63965885rem;
  padding-right: 1.06609808rem;
  line-height: 2.26012793rem;
}
.pu_header .right span.icon {
  font-size: 0.93816631rem;
}
.pu_content {
  padding-top: 2.26012793rem;
  padding-bottom: 2.26012793rem;
}
.spinnerWap {
  margin: 0 auto;
  text-align: center;
  background-color: #eee;
  padding: 0 0.12793177rem;
  height: 1.49253731rem;
  padding-top: 0.29850746rem;
  color: #fff;
}
.spinnerWap .jz {
  margin-left: 0.42643923rem;
  color: #999;
}
.vux-tab {
  height: 1.87633262rem;
}
.vux-tab .vux-tab-item {
  line-height: 1.87633262rem;
  font-size: 0.59701493rem;
}
/*数量选择*/
.weui-cells {
  margin: 0;
  font-size: 0.59701493rem;
}
.weui-cell {
  padding: 0.21321962rem 0;
  margin-top: 0.12793177rem;
}
.weui-cell p {
  font-size: 0.59701493rem;
  padding-left: 0.63965885rem;
  padding-top: 0.21321962rem;
}
.vux-number-selector-sub {
  padding: 0.17057569rem 0.34115139rem 0.08528785rem 0.34115139rem;
}
.vux-number-selector {
  height: 0.85287846rem;
  font-size: 1.06609808rem;
  line-height: 0.76759062rem;
  border: none;
}
.vux-number-input {
  height: 0.85287846rem;
  font-size: 0.68230277rem;
  padding: 0.12793177rem 0;
  margin-top: 0.17057569rem;
}
.vux-number-selector-plus {
  padding: 0.12793177rem 0.34115139rem;
  margin-right: 0.21321962rem;
}
.weui-cells:before {
  height: 0;
  border: none;
}
.weui-cells:after {
  height: 0;
  border: none;
}
/*选择型号弹窗*/
.content .vux-popup-dialog {
  background-color: transparent;
}
/*轮播*/
.vux-slider > .vux-swiper > .vux-swiper-item > a > .vux-swiper-desc {
  display: none;
}
.vux-slider > .vux-swiper > .vux-swiper-item > a > .vux-img {
  background-size: contain;
}
/*tab切换*/
.vux-tab .vux-tab-item {
  background-size: 0 0;
}
.vux-tab .vux-tab-item {
  line-height: 1.87633262rem;
  font-size: 0.59701493rem;
}
.tab_header .vux-tab .vux-tab-item {
  font-size: 0.76759062rem;
}
/*mint-ui*/
.mint-indicator-mask {
  z-index: 99998;
}
/*店铺信息弹窗*/
/*.vux-popup-dialog {
	overflow-y: hidden;
}*/
/*编辑地址页面*/
.vux-cell-box:before {
  border-top: none;
}
.weui-cell__ft:after {
  right: 0.63965885rem !important;
}
.weui-cells {
  font-size: 0.68230277rem;
}
.inputWrap .weui-icon-success {
  color: red;
}
.vux-cell-placeholder {
  color: #ddd;
}
/*确认订单选择按钮*/
.itemInfo .weui-icon-success {
  font-size: 0.85287846rem;
  color: red;
  margin-bottom: 0.12793177rem;
}
.itemInfo .weui-icon-circle {
  font-size: 0.85287846rem;
  margin-bottom: 0.12793177rem;
}
/*登录页面*/
.login_buttom .vux-divider {
  padding: 0.42643923rem 0;
  color: #999;
  font-size: 0.59701493rem;
}
.login_tabWrap .vux-tab .vux-tab-item {
  font-size: 0.68230277rem;
}
/*编辑个人信息页面*/
.sexli .weui-cell p {
  padding: 0;
}
.sexli .weui-cell_access .weui-cell__ft {
  padding-right: 1.06609808rem;
}
.sexli .weui-cell_access .weui-cell__ft:after {
  border-color: #fff;
}
.vux-prompt {
  padding-top: 1.6em;
}
/*店铺热销商品*/
.sellHot .vux-divider {
  font-size: 0.68230277rem;
  color: #999;
  padding-bottom: 0.21321962rem;
}
/*首页热销商品*/
.goods_hot .vux-divider {
  /*color: #999;*/
  /*padding: 2/@rem 8/@rem 1px 8/@rem;*/
  margin: 0 auto;
}
.goods_hot .vux-divider:before {
  background-position: right 4px top 50%;
  width: 20%;
  color: black;
}
.goods_hot .vux-divider:after {
  background-position: left 4px top 50%;
  width: 20%;
}
.weui-wepay-flow__li_done p.weui-wepay-flow__title-right,
p.weui-wepay-flow__title-right {
  width: 11.08742004rem;
  height: auto;
  white-space: normal;
}
img.ximg-error {
  border: 1px solid #eee;
  background-color: #eee;
}
img.ximg-class {
  /*background-image: url("../../src/assets/loading.gif");
         background-repeat: no-repeat;
         background-position: center;
         background-size: cover;*/
  border: 1px solid #eee;
  background-color: #eee;
}
/* 轮播*/
.page-swipe .mint-swipe {
  height: 6.90831557rem;
  color: red;
  background-color: #fff;
}
.page-swipe {
  overflow: hidden;
}
.page-swipe img {
  width: 100%;
  height: auto;
}
.mint-swipe-indicators div.is-active {
  background-color: #04BE02;
  opacity: 1;
}
/*热销标题*/
.vux-divider {
  /* background-color: #fff;*/
  font-size: 0.59701493rem;
}
/*轮播*/
.vux-swiper-item img {
  width: 100%;
}
body {
  background-color: #f4f4f4 !important;
}
/*普通按钮*/
.comBt {
  color: #04BE02 !important;
  border: 1px solid #04BE02 !important;
}
.color {
  color: #04BE02 !important;
}
#foot {
  /*background-color: @color-orange;*/
}
#foot a div {
  color: #545352;
}
#foot a.router-link-active div {
  color: #04BE02;
}
/*我的订单状态提示*/
.order .content .list_li .title .right.price {
  color: #04BE02 !important;
}
/*首页*/
#index_classfiy a p {
  color: #545352;
}
/*所有tab切换*/
.vux-tab .vux-tab-item.vux-tab-selected {
  color: #04BE02;
}
/*分类页面*/
#Category .list {
  background-color: #eee;
}
#Category .list li {
  background-color: #f5f3f3;
}
#Category .list li.active {
  background-color: #fff;
  color: #04BE02;
  border-left: 2px solid #04BE02;
}
#goodInfo .tag span {
  background-color: #04BE02;
  color: #fff;
}
.iconActive {
  color: #04BE02 !important;
}
/*列表*/
#tabWrap .up.active i {
  color: #04BE02;
}
/*所有单选*/
.success {
  color: #04BE02 !important;
}
/*所有结算按钮*/
.accountAtn {
  background-color: #04BE02 !important;
}
/*所有提示数量头标*/
.ft {
  background-color: #04BE02 !important;
}
/*购物车*/
li.noGoods {
  background-color: #f4f4f4 !important;
  text-align: center !important;
}
/*选择数量*/
.vux-number-selector svg {
  fill: #04BE02;
}
/*物流*/
.weui-wepay-flow__li_done .weui-wepay-flow__state {
  background-color: #04BE02 !important;
}
.weui-wepay-flow__process {
  background-color: #04BE02 !important;
}
.fade-enter-active,
.fade-leave-active {
  opacity: 1;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}



